/** 
 *
 * interface
 * */

/** -------  -------
 * 
 * 1. WGS84 GPS Google 
 * 2. GCJ-02
 * 3. BD-09
 *  AMap.convertFrom() 
 *
 *  https://lbs.amap.com/api/jsapi-v2/documentation#convertfrom
 *
 *  number
 */
// 
export type BaiduLng = number;
export type BaiduLat = number;

// 
export type WGS84Lng = number;
export type WGS84Lat = number;
export type WGS84 = WGS84Lng | WGS84Lat;

//  (GCJ02)
export type AMapLng = number;
export type AMapLat = number;
export type GCJ02 = AMapLng | AMapLat;

/**
 * GPS
 */
export class LocationPoint {
  constructor(latitude: number, longitude: number) {
    this.latitude = latitude;
    this.longitude = longitude;
  }

  latitude: number; // 
  longitude: number; // 
}

/**
 * 
 */
export class LocationInfo extends LocationPoint {
  address: string;
  constructor(latitude: number, longitude: number, address: string) {
    super(latitude, longitude);
    this.address = address;
  }
}

/**
 * 
 */
export type MapType =
  | "street-map"
  | "satellite"
  | "traffic-info"
  | "street-map-info"
  | "satellite-info";

/**
 * 
 */
export class selectInfo {
  lat: number;
  lng: number;
  address: string;
  name: string;
  start_time: String;
  end_time: String;
  picOriginalName: string;
  fireType: string;
  fireIntensity: string;
  victim: number;
  beaufort: number;
  money: number;
  fireBrigade: number;
  rainfall: number;
  temperature: number;
  humidity: number;
  windDirection: string;
  province: string;
  city: string;
  area: string;
  picDatasetName: string;

  constructor(lat: number, lng: number, address: string, name: string, start_time: String, end_time: String, picOriginalName: string,
    fireType: string, fireIntensity: string, victim: number, beaufort: number, money: number, fireBrigade: number, rainfall: number,
    temperature: number, humidity: number, windDirection: string, province: string, city: string, area: string, picDatasetName: string) {
    this.lat = lat;
    this.lng = lng;
    this.address = address;
    this.name = name;
    this.start_time = start_time;
    this.end_time = end_time;
    this.picOriginalName = picOriginalName;
    this.fireType = fireType;
    this.fireIntensity = fireIntensity;
    this.victim = victim;
    this.beaufort = beaufort;
    this.money = money;
    this.fireBrigade = fireBrigade;
    this.rainfall = rainfall;
    this.temperature = temperature;
    this.humidity = humidity;
    this.windDirection = windDirection;
    this.province = province;
    this.city = city;
    this.area = area;
    this.picDatasetName = picDatasetName;
  }
}

/**
 * 
 */
export class PicForm {
  lat: number; // 
  lng: number; // 
  period: [Date, Date]; // 
  fireType: "forest" | "building" | "indoor" | "other"; // 
  fireIntensity: "small" | "medium" | "large"; // 
  victim: number; // 
  province: string[]; // 
  beaufort: number; //
  windDirection: string; //
  rainfall: number; // 
  temperature: number; // 
  humidity: number; // 
  fireBrigade: number; // 
  money: number; // 
  address: string // 

  constructor(
    lat: number,
    lng: number,
    period: [Date, Date],
    fireType: "forest" | "building" | "indoor" | "other",
    fireIntensity: "small" | "medium" | "large",
    victim: number,
    province: string[],
    beaufort: number,
    windDirection: string,
    rainfall: number,
    temperature: number,
    humidity: number,
    fireBrigade: number,
    money: number,
    address: string
  ) {
    this.lat = lat;
    this.lng = lng;
    this.period = period;
    this.fireType = fireType;
    this.fireIntensity = fireIntensity;
    this.victim = victim;
    this.province = province;
    this.beaufort = beaufort;
    this.windDirection = windDirection;
    this.rainfall = rainfall;
    this.temperature = temperature;
    this.humidity = humidity;
    this.fireBrigade = fireBrigade;
    this.money = money;
    this.address = address;
  }
}

/**
 * 
 */
export class optionInfo {
  img: string; // 
  info: boolean; // 
  outputSize: number; // (0.1 - 1)
  outputType: string; // (jpeg || png || webp)
  canScale: boolean; // 
  autoCrop: boolean; // 
  autoCropWidth: number; // 
  autoCropHeight: number; // 
  fixedBox: boolean; //  
  fixed: boolean; // 
  fixedNumber: number[]; // 
  full: boolean; // 
  canMove: boolean; //
  canMoveBox: boolean; // 
  original: boolean; // 
  centerBox: boolean; // 
  high: boolean; //dpr 
  infoTrue: boolean; // true  false 
  maxImgSize: number; //
  enlarge: number; //
  mode: string; //

  constructor(
    img: string,
    info: boolean,
    outputSize: number,
    outputType: string,
    canScale: boolean,
    autoCrop: boolean,
    autoCropWidth: number,
    autoCropHeight: number,
    fixedBox: boolean,
    fixed: boolean,
    fixedNumber: number[],
    full: boolean,
    canMove: boolean,
    canMoveBox: boolean,
    original: boolean,
    centerBox: boolean,
    high: boolean,
    infoTrue: boolean,
    maxImgSize: number,
    enlarge: number,
    mode: string
  ) {
    this.img = img;
    this.info = info;
    this.outputSize = outputSize;
    this.outputType = outputType;
    this.canScale = canScale;
    this.autoCrop = autoCrop;
    this.autoCropWidth = autoCropWidth;
    this.autoCropHeight = autoCropHeight;
    this.fixedBox = fixedBox;
    this.fixed = fixed;
    this.fixedNumber = fixedNumber;
    this.full = full;
    this.canMove = canMove;
    this.canMoveBox = canMoveBox;
    this.original = original;
    this.centerBox = centerBox;
    this.high = high;
    this.infoTrue = infoTrue;
    this.maxImgSize = maxImgSize;
    this.enlarge = enlarge;
    this.mode = mode;
  }
}

export class DetectBox {
  type: string;
  x1: number;
  y1: number;
  x2: number;
  y2: number;
  confidence: string;


  constructor(type: string, x1: number, y1: number, x2: number, y2: number, confidence: string) {
      this.type = type;
      this.x1 = x1;
      this.y1 = y1;
      this.x2 = x2;
      this.y2 = y2;
      this.confidence = confidence;
  }
}

export class DetectResponse{
  statusMessage: string;
  detectBox: DetectBox[];
  detectImage: string;

  constructor(statusMessage: string, detectBox: DetectBox[], detectImage: string) {
      this.statusMessage = statusMessage;
      this.detectBox = detectBox;
      this.detectImage = detectImage;
  }
}

export class SearchInfo {
  title: string;
  address: string;
  province: string;
  city:string;
  area:string;
  info: object;

  constructor(title: string, address: string, province: string, city:string, area:string, info: object) {
      this.title = title;
      this.address = address;
      this.province = province;
      this.city= city;
      this.area = area;
      this.info = info;
  }
}